<template>
  <Side_Bar>
    <div class="ml">
      <div class="row">
        <div class="col-lg-6">
          <ExchangeDetailDialog :show="showDialogED"
                      :cancel="cancelDialogDetail"
                      v-if="showDialogED" class="modal">
            <table class="table">
              <thead>
              <tr class="header">
                <td>M chi tit</td>
                <td>M Sch 1</td>
                <td>TT Sch 1 trc GD</td>
                <td>TT Sch 1 sau GD</td>
                <td>M Sch 2</td>
                <td>TT Sch 2 trc GD</td>
                <td>TT Sch 2 sau GD</td>
                <td>Thi gian to</td>
                <td>Hn GD</td>
                <td>Trng thi</td>
                <td v-if="!showEditED">Hy</td>
                <td v-if="showEditED">Lu</td>
              </tr>
              </thead>
              <tbody v-for="item of listExchangeDetail" :key="item.id">
              <tr v-if="!showEditED">
                <td>{{ item.id}}</td>
                <td>{{ item.book1Id}}</td>
                <td v-if="item.beforeStatusBook1==null">Cha cp nht</td>
                <td v-else>{{ item.beforeStatusBook1}}</td>
                <td v-if="item.afterStatusBook1==null">Cha cp nht</td>
                <td v-else>{{ item.afterStatusBook1}}</td>
                <td>{{ item.book2Id}}</td>
                <td v-if="item.beforeStatusBook2==null">Cha cp nht</td>
                <td v-else>{{ item.beforeStatusBook2}}</td>
                <td v-if="item.afterStatusBook2==null">Cha cp nht</td>
                <td v-else>{{ item.afterStatusBook2}}</td>
                <td>{{ item.requestTime |  format}}</td>
                <td>{{ item.expiredDate |  formatDate}}</td>
                <td v-if="item.status == 'Trading'" ><span class="role approved">ANG GD</span></td>
                <td v-if="item.status == 'Complete'" ><span class="role approved">HON THNH</span></td>
                <td v-if="item.status == 'Cancel'" ><span class="role denied"> HY</span></td>
                <td v-if="item.status == 'Waiting'" ><span class="role waiting">ANG I</span></td>
                <td v-if="item.status == 'Waiting'"><button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="HandleCancelExchangeDetail(item.id, item.exchangeId)">Hy</button></td>
              </tr>
              <tr v-if="showEditED && item.status != 'Cancel' && item.status != 'Waiting'" >
                <td>{{ item.id}}</td>
                <td>{{ item.book1Id}}</td>
                <td><input type="text" maxlength="50" placeholder="Nhp tnh trng sch" class="editInput" v-model="item.beforeStatusBook1"></td>
                <td><input type="text" maxlength="50" placeholder="Nhp tnh trng sch" class="editInput" v-model="item.afterStatusBook1"></td>
                <td>{{ item.book2Id}}</td>
                <td><input type="text" maxlength="50" placeholder="Nhp tnh trng sch" class="editInput" v-model="item.beforeStatusBook2"></td>
                <td><input type="text" maxlength="50" placeholder="Nhp tnh trng sch" class="editInput" v-model="item.afterStatusBook2"></td>
                <td>{{ item.requestTime |  format}}</td>
                <td>{{ item.expiredDate |  formatDate}}</td>
                <td v-if="item.status == 'Trading'" ><span class="role approved">ANG GD</span></td>
                <td v-if="item.status == 'Complete'" ><span class="role approved">HON THNH</span></td>
                <td v-if="item.status == 'Cancel'" ><span class="role denied"> HY</span></td>
                <td v-if="item.status == 'Waiting'" ><span class="role waiting">ANG I</span></td>
                <td><button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="editExchangeDetail(item.id, item.exchangeId)">LU</button>  </td>
              </tr>
              </tbody>
            </table>
            <div class="divBtn">
              <button v-if="!showEditED" class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router btnUpdate" v-on:click="showEditED = true">CP NHT</button>
              <button v-if="showEditED" class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router btnUpdate" v-on:click="showEditED = false">HY CP NHT</button>
            </div>
          </ExchangeDetailDialog>
          <ExchangeBillDialog :show="showDialogBD"
                                :cancel="cancelDialogBill"
                                v-if="showDialogBD" class="modal">
            <table class="table">
              <thead>
              <tr class="header">
                <td>M ha n</td>
                <td>M/Tn KH</td>
                <td>Tng sch</td>
                <td>Ph cc</td>
                <td>Ph ship</td>
                <td>Ph dch v</td>
                <td>Tng tin</td>
                <td>TT Thanh ton</td>
                <td>Ngy thanh ton</td>
                <td>Phng thc thanh ton</td>
              </tr>
              </thead>
              <tbody v-for="item of listExchangeBills" :key="item.id">
              <tr>
                <td>{{ item.id}}</td>
                <td>{{item.userId}}/{{item.user.fullname}}</td>
                <td>{{item.totalBook}}</td>
                <td>{{item.depositFee.toLocaleString()}}</td>
                <td>{{item.feeId1Navigation.price.toLocaleString()}}</td>
                <td v-if="item.feeId3Navigation">{{item.feeId2Navigation.price.toLocaleString()}} + {{item.totalBook}}x{{item.feeId3Navigation.price}}</td>
                <td v-else>{{item.feeId2Navigation.price.toLocaleString()}}</td>
                <td>{{item.totalAmount}}</td>
                <td v-if="item.isPaid"><span class="role paid"> THANH TON</span></td>
                <td v-else><span class="role notPaid">CHA THANH TON</span></td>
                <td v-if="item.paidDate">{{item.paidDate}}</td>
                <td v-else>Cha thanh ton</td>
                <td>{{item.payments}}</td>
              </tr>
              </tbody>
            </table>
          </ExchangeBillDialog>
          <UpdateExchangeDialog :show="showDialogUE" v-if="showDialogUE" class="modal">
            <div class="topDialog">
              <div class="dialogTitle">CP NHT GIAO DCH S {{exchange.id}}</div>
              <button class="dialogExit" v-on:click="cancelDialogUE">X</button>
            </div>
            <div class="updateBody">
              <div class="updateLeft">
                <div class="customer">KHCH HNG 1</div>
                <label class="labelFee">TT Vn Chuyn: </label>
                <select class="sl" v-model="exchange.storageStatus1">
                  <option v-bind:value="item.id" v-for="item of listStatus" :key="item.id">{{ item.name }}</option>
                </select><br>
                <br>
                <label class="labelFee">Ngy nhn: </label>
                <input v-if="exchange.storageStatus1 == 'Received'" type="date" class="sl" required v-model="exchange.receiveDate1">
                <input v-else type="date" disabled class="sl" required v-model="exchange.receiveDate1"><br>
                <br>
                <label class="labelFee">Ngy gi: </label>
                <input v-if="exchange.storageStatus1 == 'Sent'" type="date" class="sl" required v-model="exchange.sendDate1">
                <input v-else type="date" class="sl" disabled required v-model="exchange.sendDate1"><br>
                <br>
                <label class="labelFee">Ngy thu hi: </label>
                <input v-if="exchange.storageStatus1 == 'Recall'" type="date" class="sl" required v-model="exchange.recallDate1">
                <input v-else type="date" class="sl" disabled required v-model="exchange.recallDate1"><br>
                <br>
                <label class="labelFee">Ngy hon tr: </label>
                <input v-if="exchange.storageStatus1 == 'Refund'"  type="date" class="sl" required v-model="exchange.refundDate1">
                <input v-else type="date" class="sl" disabled required v-model="exchange.refundDate1"><br>
              </div>
              <div class="updateRight">
                <div class="customer">KHCH HNG 2</div>
                <label class="labelFee">TT Vn Chuyn: </label>
                <select class="sl" v-model="exchange.storageStatus2">
                  <option v-bind:value="item.id" v-for="item of listStatus" :key="item.id">{{ item.name }}</option>
                </select><br>
                <br>
                <label class="labelFee">Ngy nhn: </label>
                <input v-if="exchange.storageStatus2 == 'Received'" type="date" class="sl" required v-model="exchange.receiveDate2">
                <input v-else type="date" disabled class="sl" required v-model="exchange.receiveDate2"><br>
                <br>
                <label class="labelFee">Ngy gi: </label>
                <input v-if="exchange.storageStatus2 == 'Sent'" type="date" class="sl" required v-model="exchange.sendDate2">
                <input v-else type="date" class="sl" disabled required v-model="exchange.sendDate2"><br>
                <br>
                <label class="labelFee">Ngy thu hi: </label>
                <input v-if="exchange.storageStatus2 == 'Recall'" type="date" class="sl" required v-model="exchange.recallDate2">
                <input v-else type="date" class="sl" disabled required v-model="exchange.recallDate2"><br>
                <br>
                <label class="labelFee">Ngy hon tr: </label>
                <input v-if="exchange.storageStatus2 == 'Refund'"  type="date" class="sl" required v-model="exchange.refundDate2">
                <input v-else type="date" class="sl" disabled required v-model="exchange.refundDate2"><br>
              </div>
            </div>
            <div  class="divBtn">
              <button v-if="exchange.storageStatus1 == 'Refund' && exchange.storageStatus2 == 'Refund'" class="dialogBtn" v-on:click="HandleComplete(exchange.id)">Hon Thnh</button>
              <button v-else  class="dialogBtn" v-on:click="saveUE">Cp Nht</button>
            </div>
          </UpdateExchangeDialog>
          <div class="user-data m-b-30">
            <div class="titleMB">QUN L GIAO DCH I</div>
            <hr>
            <div class="search-transaction">
              <router-link to="/ManageTransaction/rent" class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" style="height: 50px; padding-top: 10px">Xem giao dch thu</router-link>
              <select class="selectCss" v-model="filter" @change="onchange($event)">
                <option v-bind:value="item" v-for="item of listFilter" :key="item">{{ item }}</option>
              </select>
              <div>
                <input type="number" v-model="search" placeholder="Nhp m giao dch">
                <button v-on:click="HandleSearch">Tm</button>
              </div>
            </div>
            <div v-if="totalExchanges==0 && filter == ''" class="table-responsive table-data noResult">
              Khng tm thy giao dch tng ng!
            </div>
            <div v-else class="table-responsive table-data">
              <table class="table">
                <thead>
                <tr>
                  <td>Chi tit</td>
                  <td>M GD</td>
                  <td>M - Tn KH1</td>
                  <td>TT Lu tr sch - KH1</td>
                  <td>M - Tn KH2</td>
                  <td>TT Lu tr sch - KH2</td>
                  <td>Ngy to</td>
                  <td>Trng thi</td>
                  <td>Ha n</td>
                  <td>Cp nht</td>
                </tr>
                </thead>
                <tbody v-for="item of listExchanges" :key="item.id">
                <tr>
                  <td>
                    <button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="openDialogDetail(item.id)">XEM</button>
                  </td>
                  <td>{{ item.id }}</td>
                  <td>{{ item.userId1 }} - {{ item.userId1Navigation.fullname }}</td>
                  <td v-if="item.storageStatus1 == 'Waiting'" ><span class="role tradingWaiting">ANG I</span></td>
                  <td v-if="item.storageStatus1 == 'Received'"><span class="role tradingReceived"> NHN - {{item.receiveDate1|formatDate}}</span></td>
                  <td v-if="item.storageStatus1 == 'Sent'" ><span class="role tradingSent"> GI - {{item.sendDate1|formatDate}}</span></td>
                  <td v-if="item.storageStatus1 == 'Recall'" ><span class="role tradingRecall"> THU HI - {{item.recallDate1|formatDate}}</span></td>
                  <td v-if="item.storageStatus1 == 'Refund'" ><span class="role tradingRefund"> HON TR - {{item.refundDate1|formatDate}}</span></td>
                  <td>{{ item.userId2 }} - {{ item.userId2Navigation.fullname }}</td>
                  <td v-if="item.storageStatus2 == 'Waiting'" ><span class="role tradingWaiting">ANG I</span></td>
                  <td v-if="item.storageStatus2 == 'Received'" ><span class="role tradingReceived"> NHN - {{item.receiveDate2|formatDate}}</span></td>
                  <td v-if="item.storageStatus2 == 'Sent'" ><span class="role tradingSent"> GI - {{item.sendDate2|formatDate}}</span></td>
                  <td v-if="item.storageStatus2 == 'Recall'" ><span class="role tradingRecall"> THU HI - {{item.recallDate2|formatDate}}</span></td>
                  <td v-if="item.storageStatus2 == 'Refund'" ><span class="role tradingRefund"> HON TR - {{item.refundDate2|formatDate}}</span></td>
                  <td>{{ item.date | formatDate}}</td>
                  <td v-if="item.status == 'Trading'"><span class="role approved">ANG GD</span></td>
                  <td v-if="item.status == 'Complete'"><span class="role complete">XONG</span></td>
                  <td v-if="item.status == 'Cancel'"><span class="role denied"> HY</span></td>
                  <td v-if="item.status == 'Waiting'"><span class="role waiting">ANG I</span></td>
                  <td>
                    <button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="openDialogBill(item.id)">XEM</button>
                  </td>
                  <td v-if="item.status == 'Waiting'">
                    <button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="HandleTrading(item.id)">DUYT</button>
                    <button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="HandleCanCelExchange(item.id)">Hy</button>
                  </td>
                  <td v-if="item.status == 'Trading'">
                    <button class="au-btn au-btn-icon au-btn--brown au-btn--small btn-router" v-on:click="openDialogUE(item.id)">CP NHT</button>
                  </td>
                </tr>
                </tbody>
              </table>
              <div class="paging-transaction">
                <b-pagination v-if="filter==''" class="page-number" @input="getExchanges" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
                <b-pagination v-if="filter=='Tt C'" class="page-number" @input="getExchanges" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
                <b-pagination v-if="filter=='ang i'" class="page-number" @input="getExchangeWaiting" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
                <b-pagination v-if="filter=='ang Giao Dch'" class="page-number" @input="getExchangeTrading" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
                <b-pagination v-if="filter==' Hon Thnh'" class="page-number" @input="getExchangeComplete" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
                <b-pagination v-if="filter==' Hy'" class="page-number" @input="getExchangeCancel" v-model="page" :total-rows="totalExchanges"
                              :per-page="10">
                  <template #first-text><span style="color: #9D6B54;">&lsaquo;&lsaquo;</span></template>
                  <template #prev-text><span style="color: #9D6B54;">&lsaquo;</span></template>
                  <template #next-text><span style="color: #9D6B54;">&rsaquo;</span></template>
                  <template #last-text><span style="color: #9D6B54;">&rsaquo;&rsaquo;</span></template>
                  <template #page="{ page, active }">
                    <b v-if="active" style="color: white;">{{ page }} </b>
                    <b v-else style="color: #9D6B54;">{{ page }}</b>
                  </template>
                </b-pagination>
              </div>
            </div>
          </div>
        </div>
        <LoadingDialog v-show="spinner"></LoadingDialog>
      </div>
    </div>
  </Side_Bar>
</template>

<script>
import apiFactory from "@/config/apiFactory";
import {API_MANAGE_TRANSACTION, API_TRANSACTION} from "@/constant/constant-api";
import Side_Bar from "../../components/Side_Bar";
import LoadingDialog from "@/components/LoadingDialog";
import ExchangeDetailDialog from "@/components/ExchangeDetailDialog";
import ExchangeBillDialog from "@/components/ExchangeBillDialog";
import UpdateExchangeDialog from "@/components/UpdateExchangeDialog";

export default {
  name: "GetExchanges",
  components: {Side_Bar, LoadingDialog, ExchangeDetailDialog, ExchangeBillDialog, UpdateExchangeDialog},
  data() {
    return {
      listExchanges: '',
      totalExchanges: '',
      listExchangeDetail: '',
      listExchangeBills: '',
      exchange: '',
      search: '',
      isSearch: '',
      spinner: false,
      filter: 'Tt C',
      listFilter: ['Tt C', 'ang i', 'ang Giao Dch', ' Hon Thnh', ' Hy'],
      listStatus: [{id: 'Waiting', name: 'ang i'},{id: 'Received', name: ' Nhn'},{id: 'Sent', name: ' Gi'},{id: 'Recall', name: ' Thu Hi'},{id: 'Refund', name: ' Hon Tr'}],
      page: '',
      showDialogED: false,
      showDialogBD: false,
      showDialogUE: false,
      showEditED: false,
      beforeStatusBook1: '',
      afterStatusBook1: '',
      beforeStatusBook2: '',
      afterStatusBook2: '',
    }
  },
  created() {
    this.isSearch = false
    this.getExchanges(1)
  },
  methods: {
    onchange(e){
      this.isSearch = false
      this.search = ''
      if(e.target.value === 'Tt C'){
        this.getExchanges(1)
      }
      if(e.target.value=== 'ang i'){
        this.getExchangeWaiting(1)
      }
      if(e.target.value === 'ang Giao Dch'){
        this.getExchangeTrading(1)
      }
      if(e.target.value === ' Hon Thnh'){
        this.getExchangeComplete(1)
      }
      if(e.target.value === ' Hy'){
        this.getExchangeCancel(1)
      }
    },
    getExchanges(pageNumber) {
      this.spinner = true
      if(this.isSearch){
        apiFactory.callApi(API_MANAGE_TRANSACTION.SEARCH_EXCHANGE + pageNumber, 'POST', {
          id: this.search
        }).then((res) => {
          this.listExchanges = res.data.data
          this.totalExchanges = res.data.numberOfRecords
          this.page = pageNumber
          this.spinner = false
        }).catch(() => {
        });
      }else{
        apiFactory.callApi(API_MANAGE_TRANSACTION.LIST_EXCHANGE + pageNumber, 'GET', {}).then((res) => {
          this.listExchanges = res.data.data
          this.totalExchanges = res.data.numberOfRecords
          this.page = pageNumber
          this.spinner = false
        }).catch(() => {
        });
      }
    },
    getExchangeWaiting(pageNumber){
      this.spinner = true
      apiFactory.callApi(API_MANAGE_TRANSACTION.WAITING_EXCHANGE + pageNumber, 'GET', {}).then((res) => {
        this.listExchanges = res.data.data
        this.totalExchanges = res.data.numberOfRecords
        this.page = pageNumber
        this.spinner = false
      }).catch(() => {
      });
    },
    getExchangeTrading(pageNumber){
      this.spinner = true
      apiFactory.callApi(API_MANAGE_TRANSACTION.TRADING_EXCHANGE + pageNumber, 'GET', {}).then((res) => {
        this.listExchanges = res.data.data
        this.totalExchanges = res.data.numberOfRecords
        this.page = pageNumber
        this.spinner = false
      }).catch(() => {
      });
    },
    getExchangeComplete(pageNumber){
      this.spinner = true
      apiFactory.callApi(API_MANAGE_TRANSACTION.COMPLETE_EXCHANGE + pageNumber, 'GET', {}).then((res) => {
        this.listExchanges = res.data.data
        this.totalExchanges = res.data.numberOfRecords
        this.page = pageNumber
        this.spinner = false
      }).catch(() => {
      });
    },
    getExchangeCancel(pageNumber){
      this.spinner = true
      apiFactory.callApi(API_MANAGE_TRANSACTION.CANCEL_EXCHANGE + pageNumber, 'GET', {}).then((res) => {
        this.listExchanges = res.data.data
        this.totalExchanges = res.data.numberOfRecords
        this.page = pageNumber
        this.spinner = false
      }).catch(() => {
      });
    },
    getExchangeDetail(exchangeId){
      this.listExchangeDetail = ''
      apiFactory.callApi(API_MANAGE_TRANSACTION.DETAIL_EXCHANGE + exchangeId, 'GET', {}).then((res) => {
        this.listExchangeDetail = res.data.data
      }).catch(() => {
      });
    },
    getExchangeBill(exchangeId){
      this.listExchangeBills = ''
      apiFactory.callApi(API_MANAGE_TRANSACTION.BILL_EXCHANGE + exchangeId, 'GET', {}).then((res) => {
        this.listExchangeBills = res.data.data
      }).catch(() => {
      });
    },
    getExchangeById(exchangeId){
      this.exchange = ''
      apiFactory.callApi(API_MANAGE_TRANSACTION.EXCHANGE_BYID + exchangeId, 'GET', {}).then((res) => {
        this.exchange = res.data.data
      }).catch(() => {
      });
    },
    HandleSearch(){
      if (!this.search) {
        this.filter= 'Tt C'
        this.isSearch = false;
      } else {
        this.filter= ''
        this.isSearch = true;
      }
      return this.getExchanges(1)
    },
    HandleTrading(exchangeId){
      apiFactory.callApi(API_MANAGE_TRANSACTION.HANDLE_TRADE_EXCHANGE + exchangeId, 'PUT', {}).then((res) => {
        if (res.data.message === 'UPDATE_SUCCESS') {
          if(this.filter === ''){
            this.getExchanges(this.page)
          }
          if(this.filter === 'Tt C'){
            this.getExchanges(this.page)
          }
          if(this.filter === 'ang i'){
            this.getExchangeWaiting(this.page)
          }
        }
      }).catch(() => {
      });
    },
    HandleComplete(exchangeId){
      apiFactory.callApi(API_MANAGE_TRANSACTION.HANDLE_COMPLETE_EXCHANGE + exchangeId, 'PUT', {}).then((res) => {
        if (res.data.message === 'UPDATE_SUCCESS') {
          this.saveUE()
        }
      }).catch(() => {
      });
    },
    openDialogDetail(exchangeId){
      this.showDialogED = true;
      this.getExchangeDetail(exchangeId)
    },
    cancelDialogDetail(){
      this.showDialogED = false;
    },
    openDialogBill(exchangeId){
      this.showDialogBD = true;
      this.getExchangeBill(exchangeId)
    },
    cancelDialogBill(){
      this.showDialogBD = false;
    },
    openDialogUE(exchangeId){
      this.getExchangeById(exchangeId)
      this.showDialogUE = true;
    },
    cancelDialogUE(){
      this.showDialogUE = false;
    },
    saveUE(){
      this.spinner = true
      apiFactory.callApi(API_MANAGE_TRANSACTION.UPDATE_STATUS_EXCHANGE + this.exchange.id, 'PUT', {
        storageStatus1: this.exchange.storageStatus1,
        storageStatus2: this.exchange.storageStatus2,
        sendDate1: this.exchange.sendDate1,
        receiveDate1: this.exchange.receiveDate1,
        recallDate1: this.exchange.recallDate1,
        refundDate1: this.exchange.refundDate1,
        sendDate2: this.exchange.sendDate2,
        receiveDate2: this.exchange.receiveDate2,
        recallDate2: this.exchange.recallDate2,
        refundDate2: this.exchange.refundDate2,
      }).then((res) => {
        if (res.data.message === 'UPDATE_SUCCESS') {
          alert('Cp nht thnh cng')
          if(this.filter === ''){
            this.getExchanges(this.page)
          }
          if(this.filter === 'Tt C'){
            this.getExchanges(this.page)
          }
          if(this.filter === 'ang Giao Dch'){
            this.getExchangeTrading(this.page)
          }
          this.showDialogUE = false
        }
      }).catch(() => {
      });
      this.showDialogUE = false;
    },
    editExchangeDetail(exchangeDetailId, exchangeId){
      const item = this.listExchangeDetail.filter(list => list.id == exchangeDetailId)
      apiFactory.callApi(API_MANAGE_TRANSACTION.UPDATE_DETAIL_EXCHANGE + exchangeDetailId, 'PUT', {
        beforeStatusBook1: item[0].beforeStatusBook1,
        beforeStatusBook2: item[0].beforeStatusBook2,
        afterStatusBook1:  item[0].afterStatusBook1,
        afterStatusBook2:  item[0].afterStatusBook2,
      }).then((res) => {
        if (res.data.message === 'UPDATE_SUCCESS') {
          alert('Cp nht thnh cng!')
          this.getExchangeDetail(exchangeId)
        }
      }).catch(() => {
      });
    },
    HandleCancelExchangeDetail(exchangeDetailId, exchangeId){
      apiFactory.callApi(API_TRANSACTION.CANCEL_EXCHANGE_DETAIL + exchangeDetailId, 'PUT', {}).then((res) => {
        if (res.data.message === 'SUCCESS') {
          this.getExchangeDetail(exchangeId)
        }
      }).catch(() => {
        alert('Khng thnh cng!')
      });
    },
    HandleCanCelExchange(exchangeId){
      this.spinner = true
      apiFactory.callApi(API_TRANSACTION.CANCEL_EXCHANGE + exchangeId, 'PUT', {}).then((res) => {
        if (res.data.message === 'SUCCESS') {
          if(this.filter === ''){
            this.getExchanges(this.page)
          }
          if(this.filter === 'Tt C'){
            this.getExchanges(this.page)
          }
          if(this.filter === 'ang i'){
            this.getExchangeWaiting(this.page)
          }
        }
      }).catch(() => {
        alert('Khng thnh cng!')
      });
    }
  },
  filters: {
    formatDate(value) {
      return new Date(value).toLocaleDateString('en-GB')
    },
    format(value) {
      return new Date(value).toLocaleString('en-GB')
    },
  }
}
</script>

